<template>
    <div class="vsv-menu">
      <div class="vsv-jobFinder">
        <span>{{finder[0]}}</span>
        <input class="position" type="text" :placeholder="placeholders[0]">
        <span>{{finder[1]}}</span>
        <input class="vsv-location" type="text" :placeholder="placeholders[1]">
        <button class="vsv-search-submit">{{finder[2]}}</button>
        <button class="vsv-post-job"> <img class="vsv-job-edit" src="~assets/icns/edit_icnw.svg"/>&nbsp;{{postJob}}</button>
      </div> 
    </div>
</template>
<script>
export default {
  name: 'vsvMenu',
  data() {
    return {
      finder: [
        'find jobs now',
        'in',
        'go',
      ],
      placeholders: [
        'Position',
        'Location',
      ],
      postJob: 'Create a job offer',
    };
  },
};
</script>
<style lang="scss" scoped>
@import "~assets/css/main";
.vsv-menu {
    height: 45px;
    background: $vsv-white;
    color: $vsv-black;
    line-height: 40px;
    padding: 12px 30px 10px 30px;
}
.vsv-jobFinder {
  text-align: left;
  display:flex;
  flex-direction: row;
  font-family: $vsv-font-family;
  font-size: 14px;
  font-weight: $vsv-font-thin;
  text-transform: uppercase;

  .search-submit{
    flex: 0.2;
    text-transform: uppercase;  
  }
  .vsv-post-job{
    flex: 3 auto;
    margin-left: 30px;
    text-transform: uppercase;
  }
  input[type*='text']{
    font-size: inherit;
    font-weight: inherit; 
    line-height: 18px;
    padding: 2px 5px;
    margin: 0 30px;
    flex: 2 auto;
    border: 1px solid $vsv-gray;
  }
  button{
    font-size: inherit;
    font-weight: inherit;
    padding: 4px;
    background-color: $vsv-blue-primary;
    border-radius: 5px;
    border:none;
    text-transform: uppercase;
    color: #ffffff;
    flex: 0.5;
  }
}
.vsv-job-edit {
  height: 16px;
  margin-right: 15px;
  position: relative;
  top: 2px;
}

</style>


